//This page is used by ReactRouter to route as the home page. It will replace 'main' content.
import React from 'react';
import './HomePage.css';

//local projects stub
import {fetchedProjects} from '../data';

//Imported Components
import ProjectCell from '../components/ProjectCell';
import Footer from '../components/Footer';
import ContactForm from '../components/ContactForm';

//Prevent Scrolling
import ScrollToTopOnMount from '../components/ScrollToTopOnMount';
import ScrollableAnchor, { configureAnchors } from 'react-scrollable-anchor';
configureAnchors({offset: -60, scrollDuration: 600});




export default function HomePage(props)
{
    //Generates project cell with all given props from our fetchedProjects array
    function generateProjectCells()
    {
        let projects = fetchedProjects.map((project, index)=>{
            return <ProjectCell {...project} key={index} />
        });

        return (projects);
    }

    return(
        <>
        <main className="home-page">
        <ScrollToTopOnMount/>
            <section className="image-hero">
            </section>

            <section>
                <div className="home-container">
                <ScrollableAnchor id={'home-about'}>
                    <h2>About</h2>
                </ScrollableAnchor>
                    <p>As an avid fan of games, I have always been interested in game development. 
                        I was first introduced to game modding when I was playing the game Starsiege: Tribes. 
                        I was completely captivated by the ability to modify or create something new in the game, and I spent countless hours creating maps, mods, skins.
                        From there, I went on to learn how game engines worked, and I created mods for different games, such as FlyFF (PC), Soul Calibur 2 (Xbox), Halo 2 (Xbox), and many more.
                    </p>
                    <p>
                        In 2014, I decided to start a game development company called Blindshot Interactive with an amazing friend.
                        We envisioned a company where the community helped drive development and the members of the development team could help shape that process.
                        We spent the next two years designing and building our first project, Genesis, in Unreal Engine 4. 
                        It was a sandbox/survival game that we hoped would eventually be released on Steam.
                    </p>
                    <p>
                        While Blindshot Interactive eventually closed down, Rainy Labs was born from the experiences that I had with the awesome people that I worked with.
                        This website is a place to document and host projects and resources that I have or am currently working on.
                        Even though many of them are incomplete, I hope that you enjoy them or even learn something new while you are here.
                    </p>
                    <p className="signature">
                        - NiceRain
                    </p>
                </div>
            </section>

            <div className="middle-content">
            <section>
                <div className="home-container">
                <ScrollableAnchor id={'home-projects'}>
                    <h2>Projects</h2>
                </ScrollableAnchor>
                    <div className="project-container">
                        {generateProjectCells()}
                    </div>
                </div>
            </section>

            <section>
                <div className="home-container">
                <ScrollableAnchor id={'home-resources'}>
                    <h2>Resources</h2>
                </ScrollableAnchor>
                    {/* <div className="project-container">
                        <ProjectCell title="Circular Dependency Presentation"/>
                        <ProjectCell title="UE4 Data Type Reference"/>
                    </div> */}
                </div>
            </section>

            <section>
                <div className="home-container">
                    <ScrollableAnchor id={'home-contact'}>
                        <h2 >Contact</h2>
                    </ScrollableAnchor>
                    <ContactForm/>
                </div>
            </section>
            </div>
        </main>
        <Footer />
        </>
    )
}