import React from 'react';
import './ProjectPage.css';

import {projectPages} from '../data';
import ScrollToTopOnMount from '../components/ScrollToTopOnMount';
import Footer from '../components/Footer';
import Gallery from 'react-grid-gallery';

import ScrollableAnchor, { configureAnchors } from 'react-scrollable-anchor';
configureAnchors({offset: -60, scrollDuration: 600});

export default function ProjectPage(props)
{
    const fetchedProject = projectPages[props.projectId];

    // Sets our background image if we have one (and it's valid)
    // Image should have a strong gaussian blur applied and will be stretched.
    function setBGImage()
    {
        console.log('setting bg image');
        if(fetchedProject.projectBackgroundImage)
        {
            return {
                backgroundImage: `url(${fetchedProject.projectBackgroundImage})`
            }
        }
    }

    //Returns true if we have a single link from our response
    function generateLinksSection()
    {
        //Make sure we have at least one link
        if(fetchedProject && fetchedProject.projectLinks && fetchedProject.projectLinks.length > 0)
        {

            //Build all links in our array
            const builtLinks = fetchedProject.projectLinks.map((projectLink, index) =>{
                //Update our description tag
                let descriptionTag = ``;
                if(projectLink.description && projectLink.description !== '')
                {
                    //Only return description if we have a valid one
                    descriptionTag = ` (${projectLink.description})`;
                }

                //Return our JSX for each link
                return <li key={`link${index}`}><i className={projectLink.icon}></i>{projectLink.title}{descriptionTag}: <a href={projectLink.link}>Link</a></li>;
            });

            //Return our JSX
            return (
                <ScrollableAnchor id={'project-links'}>

                <section className="project-links project-pad">
                    <h2>Links</h2>
                        <ul>
                            {builtLinks}
                        </ul>
                </section>
                </ScrollableAnchor>
            );
        }
        return null;
    }

    //Returns true if we have a single download from our response
    function generateDownloadsSection()
    {
        //Make sure we have at least one download
        if(fetchedProject && fetchedProject.projectDownloads && fetchedProject.projectDownloads.length > 0)
        {

            //Build all links in our array
            const builtDownloads = fetchedProject.projectDownloads.map((projectDownload, index) =>{
                //Update our description tag
                let descriptionTag = ``;
                if(projectDownload.description && projectDownload.description !== '')
                {
                    //Only return description if we have a valid one
                    descriptionTag = ` (${projectDownload.description})`;
                }

                //Return our JSX for each link
                return <li key={`link${index}`}><i className={projectDownload.icon}></i>{projectDownload.title}{descriptionTag}: <a href={projectDownload.link}>Download</a></li>;
            });

            //Return our JSX
            return ( <ScrollableAnchor id={'project-downloads'}>
                <section className="project-downloads project-pad">
                    <h2>Downloads</h2>
                        <ul>
                            {builtDownloads}
                        </ul>
                </section>
                </ScrollableAnchor>
            );
        }
        return null;
    }

    //Returns true if we have at least one image in our gallery
    function galleryIsValid()
    {
        return fetchedProject &&
            fetchedProject.projectGallery &&
            fetchedProject.projectGallery.length > 0;
    }

    //Returns true if we have a trailer to display
    function trailerIsValid()
    {
        return fetchedProject &&
            fetchedProject.projectTrailer &&
            fetchedProject.projectTrailer !== null &&
            fetchedProject.projectTrailer !== '';   
    }

    return (

        <div className="project-page" style={setBGImage()}>
        <ScrollToTopOnMount/>
        <div>
            <section className="project-header">
            <div className="project-header-wrapper">
                <div className="project-header-content">
                    <div className="project-header-logo-wrapper">
                        <img src={fetchedProject.projectHeader.logo} alt={fetchedProject.projectTitle} />
                    </div>
                    <a href={fetchedProject.projectHeader.buttonAnchor}><button>{fetchedProject.projectHeader.buttonTitle}</button></a>
                </div>
            </div>
            <picture>
                <source media="(max-width:650px)" srcSet="/images/projects/template_head_mobile.jpg"></source>
                <img src={fetchedProject.projectHeader.headerFull} alt="VR"></img>
            </picture>
        
            </section>
            </div>
            {/* End Header Section */}
            <div className="project-page-wrapper">
                <div className="project-page-content">
                <main>
                <ScrollableAnchor id={'project-about'}>
                    <section className="project-info project-pad">
                        <div dangerouslySetInnerHTML={fetchedProject.projectInfo} ></div>
                    </section>
                </ScrollableAnchor>
                    {generateDownloadsSection()}
                    {generateLinksSection()}
                <ScrollableAnchor id={'project-media'}>
                    <section className="project-media">
                    {trailerIsValid() && <div className="trailer-container">
                        <iframe title={fetchedProject.projectTitle} className="trailer" src={fetchedProject.projectTrailer} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                    </div>}
                    {galleryIsValid() && <div className="media-container">
                        <Gallery images={fetchedProject.projectGallery} enableImageSelection={false}/>
                    </div>}
                    </section>
                    </ScrollableAnchor>
                </main>
                <Footer/>
                </div>
            </div>
        </div>
    );
}

ProjectPage.defaultProps = {
    projectId: 'vrportfolio'
}