import React from 'react';
import './Header.css';

import {Link} from '@reach/router';

export default function Header(props)
{
    return (
        <header>
           <Link to="/"><button className="header-logo"></button></Link>
            <nav>
                <ul>
                    <li><a href='#home-about'><button>About</button></a></li>
                    <li><a href='#home-projects'><button>Projects</button></a></li>
                    <li><a href='#home-resources'><button>Resources</button></a></li>
                    <li><a href='#home-contact'><button>Contact</button></a></li>
                </ul>
            </nav>
            {/* <button>Login</button> */}
        </header>
    );
}