import React, {useState} from 'react';
import './ContactForm.css';


export default function ContactForm(props)
{
    const mailStates = {
        SUCCESS: 'success',
        REQUEST: 'request',
        FAILURE: 'failure'
    }

    //Form Values
    const [name, setName] = useState("");
    const [subject, setSubject] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");
    const [mailState, setMailState] = useState("");

    //Function called OnSubmit
    function sendMail(e)
    {
        e.preventDefault();
        console.log(`${name}, ${subject}, ${email}, ${message}`);
        
        //Set our spinner (state)
        setMailState(mailStates.REQUEST);

        //Send our fetch
        return fetch("https://api.nicera.in/contact", {
            method: "POST", // *GET, POST, PUT, DELETE, etc.
            mode: "cors", // no-cors, cors, *same-origin
            cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
            credentials: "same-origin", // include, *same-origin, omit
            headers: {
                "Content-Type": "application/json",
            },

            body: JSON.stringify({
                "name": name,
                "subject" : subject,
                "email" : email,
                "message" : message
               }),
        })
        .then(response => response.json())
        .then(res =>{
            setMailState(mailStates.SUCCESS);
            console.log(res);
        })
        .catch(err=>{
            //Set our error
            setMailState(mailStates.FAILURE);
            console.log(err);
        }); 
    }

    //Render functions broken out for simplicity
    function updateName(e)
    {
        setName(e.target.value);
    }

    function updateSubject(e)
    {
        setSubject(e.target.value);
    }

    function updateEmail(e)
    {
        setEmail(e.target.value);

    }

    function updateMessage(e)
    {
        setMessage(e.target.value);
    }

    function setFormMessage()
    {
        switch (mailState)
        {
            case mailStates.SUCCESS: 
                return "Mail Successfully Sent!";
            case mailStates.REQUEST:
                return "Sending your message now...";
            case mailStates.FAILURE:
                return "Error: Your message failed to send.";
            default:
                return "";
        }
    }

    return (
    <>
    <p style={{textAlign:"center", color:"red"}}>{`${setFormMessage()}`}</p>
    <form className="contact-form" onSubmit={sendMail}>
        <fieldset>
            <label htmlFor="Name">Name:</label>
            <input 
                value={name}
                onChange={updateName}
                type="text" 
                name="Name" 
                id="Name" 
                required/>

            <label htmlFor="Subject">Subject:</label>
            <input 
                value={subject}
                onChange={updateSubject}
                type="text" 
                name="Subject" 
                id="Subject" 
                required/>

            <label htmlFor="Email">Email:</label>
            <input 
                value={email}
                onChange={updateEmail}
                type="email" 
                name="Email" 
                id="Email" 
                required/>

            <label htmlFor="Message">Message:</label><br />
            <textarea 
                value={message}
                onChange={updateMessage}
                name="Message" 
                rows="20" 
                cols="20" 
                id="Message"
                required>
            </textarea>
        </fieldset>
        <button type="submit">Submit</button>
    </form>
    <div className="clear"></div>    
    </>)
}