/* This is the default project cell. It is 500x320 and contains an image of the project.
 * When clicked, it changes 'main' to show the ProjectPage.js with relevant information.
 * On Mouseover, the image will 'slide' upwards about 50% to reveal title/description.
 */

 import React, {Component} from 'react';
 import './ProjectCell.css';

 import {Link} from '@reach/router';

 export default class ProjectCell extends Component
 {
    constructor(props)
    {
        super(props);

        this.state = {
            showDescription:false
        };
    }

    //Slides up image to reveal description
    displayInfo(showInfo)
    {
        this.setState({
          showDescription:showInfo  
        }, ()=>console.log('state updated'));
    }
    
    render(){
        return(
            <Link to={`/projects/${this.props.projectId}`}>
            <div className="project-cell" onMouseOver={()=>this.displayInfo(true)} onMouseOut={()=>this.displayInfo(false)}>
                <img 
                    src={this.props.logo} 
                    alt={this.props.alt} 
                    className="project-cell-logo"
                    style={this.state.showDescription ? {marginTop:'-200px'} : {paddingTop:'0'}}>
                </img>
                <div className="project-cell-description">
                    <h3>{this.props.title}</h3>
                    <p>{this.props.description}</p>
                </div>
            </div>
            </Link>
        );
    }
 }

 //Defaults
 ProjectCell.defaultProps = {
     logo: 'https://images.freeimages.com/images/small-previews/adf/sun-burst-1478549.jpg',
     alt: 'Alt Text',
     title: 'My Project',
     description: 'This is an example project description. This project is very exciting. We love exciting projects.',
     projectId: 'vrportfolio'
 };

